.login-wrapper {
  width: 100vw;
  height: 100vh;
  background: #4abd8b;
  color: #fff;
  background: -webkit-gradient(linear, left top, right bottom, from(#5a6f9c), to(#39ce7e));
  background: linear-gradient(to bottom right, #5a6f9c 0%, #39ce7e 100%);
  background: #5a6f9c fixed;
  animation-name: bg-spin, bg-color;
  animation-duration: 6s, 12s;
  animation-direction: alternate, normal;
  animation-iteration-count: infinite;
  -webkit-animation-name: bg-spin, bg-color;
  -webkit-animation-duration: 6s, 12s;
  -webkit-animation-direction: alternate, normal;
  -webkit-animation-iteration-count: infinite; }

